@media screen and (max-width: 481px) {
  .edit-addition :global .order-info {
    display: none;
  }
}
.edit-addition :global .el-form {
  margin-top: 30px;
  padding: 30px 25px;
  background-color: white;
}
@media screen and (max-width: 481px) {
  .edit-addition :global .el-form {
    padding-left: 0;
    padding-right: 0;
    margin-top: 0;
  }
}
.edit-addition :global .el-form .order-id {
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 1.6px;
  font-weight: 500;
  margin-bottom: 30px;
}
.edit-addition :global .el-form .el-button.paid {
  background-color: #60A32F;
}
@media screen and (max-width: 481px) {
  .edit-addition :global .el-form .item-wrapper {
    border: 1px solid #c6c6ba;
  }
}
.edit-addition :global .el-form .item-wrapper .item {
  display: flex;
  padding: 12px 0;
}
@media screen and (max-width: 481px) {
  .edit-addition :global .el-form .item-wrapper .item {
    flex-wrap: wrap;
    padding: 30px 20px;
  }
  .edit-addition :global .el-form .item-wrapper .item:not(.header) + .item {
    padding-top: 0;
  }
  .edit-addition :global .el-form .item-wrapper .item:not(.header) + .item::before {
    content: "";
    border-top: 1px dashed #c6c6ba;
    height: 1px;
    width: 100%;
    padding: 0 20px;
    margin-bottom: 30px;
  }
}
.edit-addition :global .el-form .item-wrapper .item > div {
  flex: 1;
  display: flex;
  align-items: center;
}
.edit-addition :global .el-form .item-wrapper .item > div:first-child {
  flex: 2;
}
.edit-addition :global .el-form .item-wrapper .item > div:last-child {
  justify-content: flex-end;
}
@media screen and (max-width: 481px) {
  .edit-addition :global .el-form .item-wrapper .item > div {
    color: #005377;
  }
}
@media screen and (max-width: 481px) {
  .edit-addition :global .el-form .item-wrapper .item .status {
    order: 3;
    flex-basis: 100%;
    margin-top: 25px;
    flex-wrap: wrap;
  }
  .edit-addition :global .el-form .item-wrapper .item .status:before {
    content: "訂單狀態";
    display: block;
    color: #3f3a2e;
    margin-bottom: 10px;
  }
}
.edit-addition :global .el-form .item-wrapper .item.header {
  padding: 0;
  height: 44px;
  line-height: 44px;
  color: #919191;
}
@media screen and (max-width: 481px) {
  .edit-addition :global .el-form .item-wrapper .item.header {
    display: none;
  }
}
@media screen and (max-width: 481px) {
  .edit-addition :global .footer {
    display: flex;
    flex-direction: column-reverse;
  }
  .edit-addition :global .footer .el-col + .el-col {
    margin-bottom: 10px;
  }
}

.payment-methods :global .el-radio {
  margin: 5px !important;
}